




































export default {
	data: {
		active: 0,
		height: 0,
		width: 0
	},
	methods:{
		swiperChange(e) {
			this.active = e.detail.current;
		}
	},
	mounted(){
		uni.getSystemInfo({
			success:res=>{
				this.height = res.screenHeight;
				this.width = res.screenWidth;
			}
		})
	}
}



